const variables = (options, formType) => [
    {
        title: "Provncia",
        valueType: "LIST",
        placeholder: "provncia",
        id: "province",
        options: options.provinces
    },
    {
        title: "Distrito",
        valueType: "LIST",
        placeholder: "distrito",
        options: options.district,
        id: "district",
    },
    ...(formType === "update-suggestion" ? [{
        title: "Unidade Sanitaria",
        valueType: "LIST",
        placeholder: "unidade sanitaria",
        options: options.facility,
        id: "ou",
    }]:[]),
    {
        title: "Nome(Designao)",
        valueType: "TEXT",
        id: "name",
        placeholder: "nome da unidade sanitria",
    },
    {
        title: "Short Name",
        valueType: "TEXT",
        id: "shortName",
        placeholder: "short name",
    },
    {
        title: "Cdigo",
        valueType: "TEXT",
        id: "code",
        placeholder: "cdigo da us",
    },
    {
        title: "Classificao",
        valueType: "LIST",
        id: "classification",
        options: options.cfts,
        placeholder: "classificao"
    }, {
        title: "Nome do utilizador",
        valueType: "TEXT",
        id: "requente",
        placeholder: "nome",
    },
    {
        title: "Email do utilizador",
        valueType: "EMAIL",
        id: "email",
        placeholder: "email",
    },
    {
        title: "Contacto do utilizador",
        valueType: "TEXT",
        id: "phone",
        placeholder: "contacto"
    },
]

export { variables }